<template>
  <div class="life-application-page">
    <div class="page-header">
      <el-button 
        @click="goBack" 
        icon="el-icon-arrow-left" 
        circle
        class="back-button"
      ></el-button>
      <h1 class="page-title"></h1>
    </div>
    
    <!--  -->
    <div class="main-content" :class="{ 'mode-quiz': quizStarted }">
      
      <!-- 1224 () -->
      <!-- flex-orderDOM? -->
      <!--  -->
      <!--  -->
      
      <!--  () -->
      <div class="quiz-section" v-if="quizStarted">
        <el-card class="section-card quiz-card">
          <div class="quiz-header">
            <h2 class="section-title"> </h2>
            <div class="quiz-progress">
              {{ currentQuestionIndex + 1 }} / {{ questions.length }}
            </div>
          </div>
          
          <div v-if="questions.length > 0" class="questions-container">
            <div 
              v-if="currentQuestion"
              :key="currentQuestion.id" 
              class="question-card active-question"
            >
              <div class="question-header">
                <span class="question-number"> {{ currentQuestionIndex + 1 }} </span>
                <el-tag 
                  v-if="answeredQuestions[currentQuestion.id]"
                  :type="answers[currentQuestion.id]?.isCorrect ? 'success' : 'danger'"
                >
                  {{ answers[currentQuestion.id]?.isCorrect ? '' : '' }}
                </el-tag>
              </div>
              
              <div class="question-content">
                <div class="question-image-container" v-if="currentQuestion.image">
                  <img :src="currentQuestion.image" class="question-image" alt="">
                </div>
                <div class="question-main">
                  <p class="question-text">{{ currentQuestion.question }}</p>
                  <div class="options">
                    <el-radio-group 
                      v-model="userAnswers[currentQuestion.id]" 
                      @change="answerQuestion(currentQuestion, $event)"
                      :disabled="answeredQuestions[currentQuestion.id]"
                      class="options-group"
                    >
                      <el-radio 
                        v-for="(option, optIndex) in currentQuestion.options" 
                        :key="optIndex" 
                        :label="option"
                        class="option-item"
                      >
                        {{ option }}
                      </el-radio>
                    </el-radio-group>
                  </div>
                </div>
              </div>

              <div v-if="answeredQuestions[currentQuestion.id]" class="answer-feedback">
                <div :class="answers[currentQuestion.id]?.isCorrect ? 'correct' : 'wrong'">
                  <i :class="answers[currentQuestion.id]?.isCorrect ? 'el-icon-success' : 'el-icon-error'"></i>
                  <span v-if="answers[currentQuestion.id]?.isCorrect">
                    
                  </span>
                  <span v-else>
                    {{ currentQuestion.correct_answer }}
                  </span>
                </div>
              </div>
              
              <!--  -->
              <div class="navigation-btns">
                 <el-button @click="prevQuestion" :disabled="currentQuestionIndex === 0" icon="el-icon-arrow-left"></el-button>
                 <el-button 
                   type="primary" 
                   @click="nextQuestion" 
                   v-if="currentQuestionIndex < questions.length - 1"
                   :disabled="!answeredQuestions[currentQuestion.id]"
                 >
                    <i class="el-icon-arrow-right el-icon--right"></i>
                 </el-button>
                 <el-button 
                   type="success" 
                   @click="finishQuiz" 
                   v-if="currentQuestionIndex === questions.length - 1"
                   :disabled="!answeredQuestions[currentQuestion.id]"
                 >
                   
                 </el-button>
              </div>
            </div>
          </div>
          
          <div v-else class="loading">
            <i class="el-icon-loading"></i>
            <p>...</p>
          </div>
        </el-card>
      </div>

      <!-- 1224 () -->
      <div class="rules-section">
        <el-card class="section-card rules-card">
          <h2 class="section-title"> 1224</h2>
          <p class="section-desc" v-if="!quizStarted">
            12882408:0020:00
            
          </p>
          
          <!--  -->
          <el-card class="conversion-rules" shadow="never">
            <h3 class="rules-title"> </h3>
            <div class="rules-content">
              <div class="rule-item">
                <div class="rule-header">
                  <i class="el-icon-sunny"></i>
                  <div class="rule-title">/</div>
                </div>
                <div class="rule-desc">12 = 240:00-12:00</div>
                <div class="rule-example" v-if="!quizStarted">8 = 08:00</div>
              </div>
              <div class="rule-item">
                <div class="rule-header">
                  <i class="el-icon-moon"></i>
                  <div class="rule-title">/</div>
                </div>
                <div class="rule-desc">24 = 12 + 12</div>
                <div class="rule-example" v-if="!quizStarted">88+12= 20:00</div>
              </div>
              <div class="rule-item">
                <div class="rule-header">
                  <i class="el-icon-refresh"></i>
                  <div class="rule-title"></div>
                </div>
                <div class="rule-desc">24  121212</div>
                <div class="rule-example" v-if="!quizStarted">20:0020-12= 8</div>
              </div>
            </div>
          </el-card>

          <!--  () -->
          <div class="start-action" v-if="!quizStarted">
             <el-button type="primary" size="large" round @click="startQuiz" class="start-btn">
                <i class="el-icon-arrow-right"></i>
             </el-button>
          </div>
        </el-card>
      </div>
    </div>
  </div>
</template>

<script>
// 
import img1 from '../images/1.jpg'
import img2 from '../images/2.jpg'
import img3 from '../images/3.jpg'
import img4 from '../images/4.jpg'

export default {
  name: 'LifeApplication',
  data() {
    return {
      studentId: null,
      questions: [],
      userAnswers: {},
      answeredQuestions: {},
      answers: {},
      quizStarted: false,
      currentQuestionIndex: 0
    }
  },
  computed: {
    currentQuestion() {
      if (this.questions.length === 0) return null;
      return this.questions[this.currentQuestionIndex];
    }
  },
  mounted() {
    const savedStudentId = localStorage.getItem('studentId')
    if (savedStudentId) {
      this.studentId = parseInt(savedStudentId)
    }
    this.loadQuestions()
    this.checkAnsweredQuestions()
  },
  methods: {
    loadQuestions() {
      // API
      this.questions = [
        {
          id: 101,
          question: '',
          options: ['5', '10', '11', '8'],
          correct_answer: '8', 
          image: img1
        },
        {
          id: 102,
          question: '',
          options: ['10', '1', '7', '9'],
          correct_answer: '7',
          image: img2
        },
        {
          id: 103,
          question: '11',
          options: ['8', '14', '10', '15'],
          correct_answer: '14',
          image: img3
        },
        {
          id: 104,
          question: '',
          options: ['9', '7', '618', '10'],
          correct_answer: '7',
          image: img4
        }
      ]
    },
    checkAnsweredQuestions() {
      const saved = localStorage.getItem(`answers_practice_${this.studentId}`)
      if (saved) {
        const parsed = JSON.parse(saved)
        this.answeredQuestions = parsed.answeredQuestions || {}
        this.answers = parsed.answers || {}
      }
    },
    async answerQuestion(question, answer) {
      if (this.answeredQuestions[question.id]) return
      
      const isCorrect = answer === question.correct_answer
      
      try {
        // 
        await this.$http.post(`/student/${this.studentId}/answers`, {
          question_id: question.id,
          answer: answer,
          is_correct: isCorrect,
          module_type: 'practice'
        })
        
        this.answeredQuestions[question.id] = true
        this.answeredQuestions = { ...this.answeredQuestions } // 
        
        this.answers[question.id] = {
          isCorrect: isCorrect,
          answer: answer
        }
        
        // 
        localStorage.setItem(`answers_practice_${this.studentId}`, JSON.stringify({
          answeredQuestions: this.answeredQuestions,
          answers: this.answers
        }))
        
        if (isCorrect) {
          this.$message.success('')
        } else {
          this.$message.error(`${question.correct_answer}`)
        }
      } catch (error) {
        console.error(':', error)
        // API
        this.answeredQuestions[question.id] = true
        this.answeredQuestions = { ...this.answeredQuestions }
        this.answers[question.id] = {
          isCorrect: isCorrect,
          answer: answer
        }
        localStorage.setItem(`answers_practice_${this.studentId}`, JSON.stringify({
          answeredQuestions: this.answeredQuestions,
          answers: this.answers
        }))
        this.$message.warning('')
      }
    },
    goBack() {
      this.$router.push('/')
    },
    startQuiz() {
      this.quizStarted = true;
      this.currentQuestionIndex = 0;
      window.scrollTo({ top: 0, behavior: 'smooth' });
    },
    nextQuestion() {
      if (this.currentQuestionIndex < this.questions.length - 1) {
        this.currentQuestionIndex++;
        window.scrollTo({ top: 0, behavior: 'smooth' });
      }
    },
    prevQuestion() {
      if (this.currentQuestionIndex > 0) {
        this.currentQuestionIndex--;
      }
    },
    finishQuiz() {
      this.$message.success('');
      // 
      // 
    }
  }
}
</script>

