<template>
  <div class="teacher-dashboard">
    <!--  -->
    <div v-if="!isLoggedIn" class="login-section">
      <el-card class="login-card">
        <h2></h2>
        <el-form :model="loginForm" label-width="80px">
          <el-form-item label="">
            <el-input v-model="loginForm.username" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="loginForm.password" type="password" placeholder=""></el-input>
          </el-form-item>
          <el-button type="primary" @click="login" style="width: 100%"></el-button>
        </el-form>
      </el-card>
    </div>
    
    <!--  -->
    <div v-else class="dashboard-content">
      <div class="header">
        <h1></h1>
        <el-button @click="logout" type="text"></el-button>
      </div>
      
      <el-tabs v-model="activeTab" @tab-click="handleTabClick">
        <!--  -->
        <el-tab-pane label="" name="statistics">
          <div class="statistics-section">
            <h2></h2>
            
            <el-row :gutter="20" class="stats-row">
              <el-col :span="6">
                <el-card class="stat-card">
                  <div class="stat-icon"></div>
                  <div class="stat-value">{{ statistics.total_students }}</div>
                  <div class="stat-label"></div>
                </el-card>
              </el-col>
              <el-col :span="6">
                <el-card class="stat-card">
                  <div class="stat-icon"></div>
                  <div class="stat-value">{{ statistics.total_answers }}</div>
                  <div class="stat-label"></div>
                </el-card>
              </el-col>
              <el-col :span="6">
                <el-card class="stat-card">
                  <div class="stat-icon"></div>
                  <div class="stat-value">{{ statistics.correct_answers }}</div>
                  <div class="stat-label"></div>
                </el-card>
              </el-col>
              <el-col :span="6">
                <el-card class="stat-card">
                  <div class="stat-icon"></div>
                  <div class="stat-value">{{ statistics.avg_score }}%</div>
                  <div class="stat-label"></div>
                </el-card>
              </el-col>
            </el-row>
            
            <h3></h3>
            <el-row :gutter="20" class="module-stats-row">
              <el-col :span="6" v-for="(stats, module) in statistics.module_stats" :key="module">
                <el-card>
                  <h4>{{ getModuleName(module) }}</h4>
                  <div class="module-stat-item">
                    <span></span>
                    <strong>{{ stats.total }}</strong>
                  </div>
                  <div class="module-stat-item">
                    <span></span>
                    <strong style="color: #67c23a">{{ stats.correct }}</strong>
                  </div>
                  <div class="module-stat-item">
                    <span></span>
                    <strong style="color: #667eea">{{ stats.accuracy }}%</strong>
                  </div>
                </el-card>
              </el-col>
            </el-row>
            
            <div class="refresh-section">
              <el-button @click="loadStatistics" type="primary" icon="el-icon-refresh"></el-button>
            </div>
          </div>
        </el-tab-pane>
        
        <!--  -->
        <el-tab-pane label="" name="students">
          <div class="students-section">
            <h2></h2>
            
            <el-table :data="students" style="width: 100%">
              <el-table-column prop="name" label="" width="150"></el-table-column>
              <el-table-column prop="class_name" label="" width="150"></el-table-column>
              <el-table-column prop="total" label="" width="120" align="center"></el-table-column>
              <el-table-column prop="correct" label="" width="120" align="center"></el-table-column>
              <el-table-column prop="score" label="" width="150" align="center">
                <template slot-scope="scope">
                  <el-progress 
                    :percentage="scope.row.score" 
                    :color="getScoreColor(scope.row.score)"
                    :stroke-width="20"
                  ></el-progress>
                  <span style="margin-left: 10px;">{{ scope.row.score }}%</span>
                </template>
              </el-table-column>
            </el-table>
            
            <div class="refresh-section">
              <el-button @click="loadStudents" type="primary" icon="el-icon-refresh"></el-button>
            </div>
          </div>
        </el-tab-pane>
        
        <!--  -->
        <el-tab-pane label="" name="wrong-questions">
          <div class="wrong-questions-section">
            <h2></h2>
            <p class="section-desc"></p>
            
            <el-table :data="wrongQuestions" style="width: 100%">
              <el-table-column type="index" label="" width="80" align="center"></el-table-column>
              <el-table-column prop="question" label="" min-width="300"></el-table-column>
              <el-table-column prop="total_attempts" label="" width="120" align="center"></el-table-column>
              <el-table-column prop="wrong_count" label="" width="120" align="center">
                <template slot-scope="scope">
                  <span style="color: #f56c6c; font-weight: bold;">{{ scope.row.wrong_count }}</span>
                </template>
              </el-table-column>
              <el-table-column prop="error_rate" label="" width="150" align="center">
                <template slot-scope="scope">
                  <el-progress 
                    :percentage="scope.row.error_rate" 
                    color="#f56c6c"
                    :stroke-width="20"
                  ></el-progress>
                  <span style="margin-left: 10px;">{{ scope.row.error_rate }}%</span>
                </template>
              </el-table-column>
              <el-table-column prop="wrong_students" label="" width="150" align="center">
                <template slot-scope="scope">
                  {{ scope.row.wrong_students.length }}
                </template>
              </el-table-column>
            </el-table>
            
            <div class="refresh-section">
              <el-button @click="loadWrongQuestions" type="primary" icon="el-icon-refresh"></el-button>
            </div>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>

<script>
export default {
  name: 'TeacherDashboard',
  data() {
    return {
      isLoggedIn: false,
      loginForm: {
        username: '',
        password: ''
      },
      activeTab: 'statistics',
      statistics: {
        total_students: 0,
        total_answers: 0,
        correct_answers: 0,
        avg_score: 0,
        module_stats: {}
      },
      students: [],
      wrongQuestions: []
    }
  },
  mounted() {
    // 
    const savedLogin = localStorage.getItem('teacherLoggedIn')
    if (savedLogin === 'true') {
      this.isLoggedIn = true
      this.loadData()
    }
  },
  methods: {
    async login() {
      if (!this.loginForm.username || !this.loginForm.password) {
        this.$message.error('')
        return
      }
      
      try {
        const res = await this.$http.post('/teacher/login', {
          username: this.loginForm.username,
          password: this.loginForm.password
        })
        
        if (res.data.success) {
          this.isLoggedIn = true
          localStorage.setItem('teacherLoggedIn', 'true')
          this.$message.success('')
          this.loadData()
        } else {
          this.$message.error(res.data.message || '')
        }
      } catch (error) {
        this.$message.error('')
      }
    },
    logout() {
      this.isLoggedIn = false
      localStorage.removeItem('teacherLoggedIn')
      this.$message.success('')
    },
    async loadData() {
      await Promise.all([
        this.loadStatistics(),
        this.loadStudents(),
        this.loadWrongQuestions()
      ])
    },
    async loadStatistics() {
      try {
        const res = await this.$http.get('/teacher/statistics')
        this.statistics = res.data
      } catch (error) {
        this.$message.error('')
      }
    },
    async loadStudents() {
      try {
        const res = await this.$http.get('/teacher/students')
        this.students = res.data.students || []
      } catch (error) {
        this.$message.error('')
      }
    },
    async loadWrongQuestions() {
      try {
        const res = await this.$http.get('/teacher/wrong-questions')
        this.wrongQuestions = res.data.wrong_questions || []
      } catch (error) {
        this.$message.error('')
      }
    },
    handleTabClick(tab) {
      this.activeTab = tab.name
      if (this.activeTab === 'statistics') {
        this.loadStatistics()
      } else if (this.activeTab === 'students') {
        this.loadStudents()
      } else if (this.activeTab === 'wrong-questions') {
        this.loadWrongQuestions()
      }
    },
    getModuleName(module) {
      const names = {
        'explore': '',
        'learn': '',
        'practice': '',
        'challenge': ''
      }
      return names[module] || module
    },
    getScoreColor(score) {
      if (score >= 80) return '#67c23a'
      if (score >= 60) return '#e6a23c'
      return '#f56c6c'
    }
  }
}
</script>

<style scoped>
.teacher-dashboard {
  min-height: 100vh;
  padding: 20px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
}

.login-section {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 80vh;
}

.login-card {
  width: 400px;
}

.login-card h2 {
  text-align: center;
  color: #667eea;
  margin-bottom: 30px;
}

.dashboard-content {
  background: white;
  border-radius: 10px;
  padding: 30px;
  box-shadow: 0 4px 6px rgba(0,0,0,0.1);
  min-height: 80vh;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
  padding-bottom: 20px;
  border-bottom: 2px solid #f0f0f0;
}

.header h1 {
  color: #667eea;
  font-size: 28px;
}

.statistics-section h2,
.students-section h2,
.wrong-questions-section h2 {
  font-size: 24px;
  color: #333;
  margin-bottom: 20px;
}

.stats-row {
  margin-bottom: 30px;
}

.stat-card {
  text-align: center;
  padding: 20px;
  transition: transform 0.3s;
}

.stat-card:hover {
  transform: translateY(-5px);
}

.stat-icon {
  font-size: 48px;
  margin-bottom: 15px;
}

.stat-value {
  font-size: 36px;
  font-weight: bold;
  color: #667eea;
  margin-bottom: 10px;
}

.stat-label {
  font-size: 16px;
  color: #666;
}

.module-stats-row {
  margin-bottom: 30px;
}

.module-stat-item {
  margin: 10px 0;
  font-size: 14px;
  color: #333;
}

.module-stat-item strong {
  margin-left: 10px;
  font-size: 16px;
}

.section-desc {
  color: #666;
  margin-bottom: 20px;
  line-height: 1.6;
}

.refresh-section {
  margin-top: 30px;
  text-align: center;
}

::v-deep .el-tabs__item {
  font-size: 18px;
  font-weight: bold;
}

::v-deep .el-tabs__item.is-active {
  color: #667eea;
}

::v-deep .el-tabs__active-bar {
  background-color: #667eea;
}
</style>

