<template>
  <div :class="['nav-menu', mode]">
    <div class="nav-content" :class="{ 'accordion-mode': true }">
      <div 
        v-for="module in modules" 
        :key="module.id"
        class="nav-item"
        :class="[module.color, { active: currentPath === module.path }]"
        @click="handleModuleClick(module.path)"
      >
        <div class="nav-item-bg"></div>
        <div class="nav-item-content">
          <div class="icon-box">
            <i :class="module.icon"></i>
          </div>
          <div class="text-box">
            <h3 class="title">{{ module.title }}</h3>
            <p class="desc">{{ module.description }}</p>
          </div>
        </div>
      </div>

      <!--  -->
      <div 
        class="nav-item icon-purple" 
        :class="{ active: currentPath === '/my-achievements' }"
        @click="handleModuleClick('/my-achievements')"
      >
        <div class="nav-item-bg"></div>
        <div class="nav-item-content">
          <div class="icon-box">
            <i class="el-icon-medal"></i>
          </div>
          <div class="text-box">
            <h3 class="title"></h3>
            <p class="desc"></p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'NavMenu',
  props: {
    mode: {
      type: String,
      default: 'center' // 'center' or 'sidebar'
    }
  },
  data() {
    return {
      modules: [
        {
          id: 'pre-explore',
          title: '',
          description: '',
          icon: 'el-icon-light-rain',
          color: 'icon-yellow',
          path: '/pre-explore'
        },
        {
          id: 'new-learning',
          title: '',
          description: '',
          icon: 'el-icon-reading',
          color: 'icon-blue',
          path: '/new-learning'
        },
        {
          id: 'time-conversion',
          title: '',
          description: '12/24',
          icon: 'el-icon-refresh',
          color: 'icon-blue',
          path: '/time-conversion'
        },
        {
          id: 'life-application',
          title: '',
          description: '',
          icon: 'el-icon-time',
          color: 'icon-green',
          path: '/life-application'
        },
        {
          id: 'challenge',
          title: '',
          description: '',
          icon: 'el-icon-trophy',
          color: 'icon-purple',
          path: '/challenge'
        }
      ]
    }
  },
  computed: {
    currentPath() {
      return this.$route.path
    }
  },
  methods: {
    handleModuleClick(path) {
      if (this.$route.path !== path) {
        this.$router.push(path)
      }
    }
  }
}
</script>

<style scoped>
.nav-menu {
  transition: all 0.6s cubic-bezier(0.25, 0.8, 0.25, 1);
  z-index: 100;
}

/* ----------------  () ---------------- */
.nav-menu.center {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 40px;
  background: transparent;
  pointer-events: none; /* () */
}

.nav-menu.center .nav-content {
  pointer-events: auto;
  width: 100%;
  max-width: 1000px;
  height: 500px;
  display: flex;
  gap: 10px;
}

.nav-menu.center .nav-item {
  flex: 1;
  position: relative;
  border-radius: 20px;
  overflow: hidden;
  cursor: pointer;
  transition: flex 0.5s ease, transform 0.3s ease;
  box-shadow: 0 10px 30px rgba(0,0,0,0.1);
}

/*  */
.nav-menu.center .nav-item:hover {
  flex: 3; /*  */
}

.nav-menu.center .nav-item-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  transition: transform 0.5s ease;
}

.nav-menu.center .nav-item-content {
  position: relative;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  color: white;
  text-align: center;
  background: rgba(0, 0, 0, 0.1); /*  */
  backdrop-filter: blur(0px);
  transition: all 0.5s ease;
}

.nav-menu.center .nav-item:hover .nav-item-content {
  background: rgba(0, 0, 0, 0);
}

.nav-menu.center .icon-box {
  font-size: 40px;
  margin-bottom: 20px;
  transition: transform 0.5s ease;
}

.nav-menu.center .nav-item:hover .icon-box {
  transform: scale(1.2);
}

.nav-menu.center .title {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 10px;
  white-space: nowrap;
}

.nav-menu.center .desc {
  font-size: 14px;
  opacity: 0;
  transform: translateY(20px);
  transition: all 0.5s ease;
  max-width: 200px;
}

.nav-menu.center .nav-item:hover .desc {
  opacity: 1;
  transform: translateY(0);
}

/* ----------------  () ---------------- */
.nav-menu.sidebar {
  position: fixed;
  top: 0;
  left: 0;
  width: 260px;
  height: 100vh;
  background: white;
  box-shadow: 4px 0 20px rgba(0,0,0,0.05);
  padding: 20px 10px;
  overflow-y: auto;
}

.nav-menu.sidebar .nav-content {
  display: flex;
  flex-direction: column;
  gap: 12px;
  height: auto;
}

.nav-menu.sidebar .nav-item {
  height: 80px; /*  */
  border-radius: 12px;
  overflow: hidden;
  cursor: pointer;
  position: relative;
  transition: all 0.3s ease;
  flex-shrink: 0;
}

.nav-menu.sidebar .nav-item:hover,
.nav-menu.sidebar .nav-item.active {
  height: 140px; /*  */
}

.nav-menu.sidebar .nav-item-content {
  position: relative;
  height: 100%;
  display: flex;
  flex-direction: row; /* ?  */
  align-items: center;
  padding: 15px;
  color: white;
}

/*  */
.nav-menu.sidebar .icon-box {
  font-size: 24px;
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(255,255,255,0.2);
  border-radius: 50%;
  margin-right: 15px;
  flex-shrink: 0;
}

.nav-menu.sidebar .text-box {
  flex: 1;
  overflow: hidden;
}

.nav-menu.sidebar .title {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 4px;
}

.nav-menu.sidebar .desc {
  font-size: 12px;
  opacity: 0.8;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  display: none; /*  */
}

.nav-menu.sidebar .nav-item:hover .desc,
.nav-menu.sidebar .nav-item.active .desc {
  display: block; /*  */
  animation: fadeIn 0.3s ease;
}

/*  */
.icon-yellow { background: linear-gradient(135deg, #f6d365 0%, #fda085 100%); }
.icon-blue { background: linear-gradient(135deg, #84fab0 0%, #8fd3f4 100%); }
.icon-green { background: linear-gradient(135deg, #a8edea 0%, #fed6e3 100%); } /*  */
.icon-purple { background: linear-gradient(135deg, #a18cd1 0%, #fbc2eb 100%); }

/*  */
.icon-yellow { background: linear-gradient(135deg, #ffd700 0%, #ffb347 100%); }
.icon-blue { background: linear-gradient(135deg, #4facfe 0%, #00f2fe 100%); }
.icon-green { background: linear-gradient(135deg, #43e97b 0%, #38f9d7 100%); }
.icon-purple { background: linear-gradient(135deg, #fa709a 0%, #fee140 100%); }

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(5px); }
  to { opacity: 1; transform: translateY(0); }
}
</style>
